html, body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: center;
}

a:link, a:visited {
  -webkit-transition: color 200ms;
  -o-transition: color 200ms;
  transition: color 200ms;
  text-decoration: none;
  color: #fc571b;
}

a:hover {
  color: #ff9066;
}

.main-logo {
  font-size: 48px;
  letter-spacing: -3px;
  border-bottom: 1px solid #ff9066;
}

.container {
  max-width: 800px;
  margin: auto;
  padding: 0 30px;
  text-align: left;
}

.loading-screen {
  width: 100%;
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  justify-items: center;
  text-align: center;
  font-size: 24px;
  color: #aaa;
  letter-spacing: 2px;
}

.loading-screen .inner {
  margin: auto;
}

.post-listing {
  padding: 30px 0;
  border-top: 1px solid #ddd;
}
.post-listing:first-child {
  border: none;
}
  .post-listing .info {
    display: inline-block;
    vertical-align: middle;
  }
  .post-listing .post-title {
    margin: 0;
  }

.byline {
  color: #aaa;
  font-size: 1rem;
  font-weight: normal;
}

.author-image {
  display: inline-block;
  width: 30px;
  height: auto;
  border-radius: 50%;
  vertical-align: middle;
  margin-right: 10px;
}

.detail-nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}


